<template>
  <div id="page-content" class="header-static center">
    <div class="container">
      <div class="row padding-onlytop-sm" style="min-width:570px">
        <input type="text" v-model="hours" />
        <input type="text" v-model="minutes" />
        <input type="text" v-model="seconds" />
        <br />
        <input type="text" v-model="year" />
        <input type="text" v-model="month" />
        <input type="text" v-model="day" />
        <input type="text" v-model="week" />
        <br />
        <textarea v-model="location" cols="70" rows="2"></textarea>
        <br />
        <input id="upload" ref="selectFile" type="file" @change="upload" />
        <input @click="downloadScreenshot" class="btn-alt active" type="button" value="" />
      </div>
      <div class="row" id="image-div">
        <div class="photo" :style="photoSrc"></div>
        <div class="background"></div>
        <div class="hours0" :style="hours0Src"></div>
        <div class="hours1" :style="hours1Src"></div>
        <div class="minutes0" :style="minutes0Src"></div>
        <div class="minutes1" :style="minutes1Src"></div>
        <div class="seconds0" :style="seconds0Src"></div>
        <div class="seconds1" :style="seconds1Src"></div>
        <div class="year0" :style="year0Src"></div>
