        <div class="month0" :style="month0Src"></div>
        <div class="month1" :style="month1Src"></div>
        <div class="text-time2">-</div>
        <div class="day0" :style="day0Src"></div>
        <div class="day1" :style="day1Src"></div>
        <div class="week" v-text="week"></div>
        <span class="location" v-text="location"></span>
        <span class="randomCode" v-text="randomCode"></span>
      </div>
    </div>
  </div>
