<script>
import html2canvas from 'html2canvas'

export default {
  data: function () {
    let now = new Date()
    return {
      hours: this.padZero(now.getHours()),
      minutes: this.padZero(now.getMinutes()),
      seconds: this.padZero(now.getSeconds()),
      year: this.padZero(now.getFullYear()),
      day: this.padZero(now.getUTCDate()),
      month: this.padZero(now.getMonth() + 1),
      week: this.getWeek(),
      location: 'xxxxxxxxxxxxxxxxxxxxx',
      photoSrc:
        'background-image:url(' +
        require('../assets/img/markPhoto/example.jpg') +
        ')',
      downloadUrl: '',
    }
  },
  computed: {
    randomCode: function () {
      const length = 14
      const characters = 'ABCDEFGHIJKLMNOPQRSTUVWXYZ0123456789'
      let result = ''
      for (let i = 0; i < length; i++) {
        result += characters.charAt(
          Math.floor(Math.random() * characters.length)
        )
      }
      return result
    },
    hours0Src: function () {
      return (
        'background-image:url(' +
        require('../assets/img/markPhoto/' +
          (this.hours.split('')[0] || 0) +
          '.png') +
        ')'
      )
    },

    hours1Src: function () {
      return (
        'background-image:url(' +
        require('../assets/img/markPhoto/' +
          (this.hours.split('')[1] || 0) +
          '.png') +
        ')'
      )
    },

    minutes0Src: function () {
      return (
        'background-image:url(' +
        require('../assets/img/markPhoto/' +
          (this.minutes.split('')[0] || 0) +
          '.png') +
        ')'
      )
    },

    minutes1Src: function () {
      return (
        'background-image:url(' +
        require('../assets/img/markPhoto/' +
          (this.minutes.split('')[1] || 0) +
          '.png') +
        ')'
      )
    },
    seconds0Src: function () {
