          '.png') +
        ')'
      )
    },
    year1Src: function () {
      return (
        'background-image:url(' +
        require('../assets/img/markPhoto/' +
          (this.year.split('')[1] || 0) +
          '.png') +
        ')'
      )
    },
    year2Src: function () {
      return (
        'background-image:url(' +
        require('../assets/img/markPhoto/' +
          (this.year.split('')[2] || 0) +
          '.png') +
        ')'
      )
    },
    year3Src: function () {
      return (
        'background-image:url(' +
        require('../assets/img/markPhoto/' +
          (this.year.split('')[3] || 0) +
          '.png') +
        ')'
      )
    },
    day0Src: function () {
      return (
        'background-image:url(' +
        require('../assets/img/markPhoto/' +
          (this.day.split('')[0] || 0) +
          '.png') +
        ')'
      )
    },
    day1Src: function () {
      return (
        'background-image:url(' +
        require('../assets/img/markPhoto/' +
          (this.day.split('')[1] || 0) +
          '.png') +
        ')'
      )
    },
    month0Src: function () {
      return (
        'background-image:url(' +
        require('../assets/img/markPhoto/' +
          (this.month.split('')[0] || 0) +
          '.png') +
        ')'
      )
    },
    month1Src: function () {
      return (
        'background-image:url(' +
        require('../assets/img/markPhoto/' +
          (this.month.split('')[1] || 0) +
          '.png') +
        ')'
      )
    },
  },
  mounted: function () {},
  methods: {
    downloadScreenshot: function () {
      html2canvas(document.querySelector('#image-div'), {
        width: 2560,
        height: 1920,
      }).then(function (canvas) {
        const screenshot = canvas.toDataURL('image/png')
        const link = document.createElement('a')
        link.download = 'screenshot.png'
        link.href = screenshot
        link.click()
      })
    },
    padZero: function (input) {
      return (input + '').padStart(2, '0')
    },
    upload: function ($event) {
      this.$refs.selectFile.dispatchEvent(new MouseEvent('click'))
      this.photoSrc =
        'background-image: url("' +
        window.URL.createObjectURL($event.target.files[0]) +
        '")'
    },
    getWeek: function () {
      let today = new Date()
      let dayOfWeek = today.getDay()

      switch (dayOfWeek) {
        case 0:
          return ''
        case 1:
          return ''
        case 2:
          return ''
        case 3:
          return ''
        case 4:
          return ''
        case 5:
          return ''
        case 6:
          return ''
        default:
          return ''
      }
    },
  },
}
</script>
